<template>
  <div id="app">
    <h1>오늘의 명언</h1>
    <span>{{quote}}</span><br>
    <span>{{"-"+speaker}}</span><br>
    <br>
     <form v-on:submit="submitForm">
      <label for="todo-date">날짜 : </label>
      <input type="date" v-model="date" id="todo-date">
      <br>
      <label for="todo-title">할일 : </label>
      <input type="text" v-model="title" id="todo-title" placeholder="간단하게 작성하세요.">
      <br>
      <b-button type="submit" variant="success">등록</b-button>
      <hr>
    </form>
    <div :style="`color: rgb(${statusCnt * 10}, 0, 0)`">
      <h2 v-if="statusCnt > 20">
        아주 바쁨
      </h2>
      <h2 v-else-if="statusCnt > 15">
        바쁨
      </h2>
      <h2 v-else-if="statusCnt > 10">
        양호
      </h2>
      <h2 v-else-if="statusCnt > 5">
        널널
      </h2>
      <h2 v-else>
        한가함
      </h2>
    </div>
    <center>
      <div v-if="list.length > 0">
        <table style="width: 400px">
          <th>완료</th>
          <th>D-day</th>
          <th>할 일</th>
          <th>마감 기한</th>
          <th></th>
          <tr v-for="item in list" :key="item.id" :class="{'option' : item.checker}" :style="`color: rgb(${255 - (item.days * 5)}, 0, 0); font-weight: ${900 - (item.days) * 10};`">
            <td><input type="checkbox" @click="changeCheck(item.id)"></td>
            <td>{{ item.days + "일"}}</td>
            <td>{{ item.title }}</td>
            <td>{{ item.date }}</td>
            <td><b-button type="button" @click="deleteTodo(item.id)" variant="success">삭제</b-button></td>
          </tr>
        </table>
      </div>
    </center>
  </div>
</template>

<script>
export default ({
  data() {
    return {
      quotes: [
        { 
          saying: "시간은 금이다.",
          name: "벤자민 프랭클린"
        }, 
        {
          saying: "산을 움직이려 하는 이는 작은 돌을 들어내는 일로 시작한다.",
          name: "공자"
        }, 
        {
          saying: "낭비한 시간에 대한 후회는 더 큰 시간 낭비이다.",
          name: "메이슨 쿨리"
        },
        {
          saying: "미래를 신뢰하지 마라, 죽은 과거는 묻어버려라, 그리고 살아있는 현재에 행동하라.",
          name: "롱펠로"
        },
        {
          saying: "시간을 단축시키는 것은 활동이요, 시간을 견디지 못하게 하는 것은 안일 함이다.",
          name: "괴테"
        },
        {
          saying: "그대의 하루 하루를 그대의 마지막 날이라고 생각하라.",
          name: "호라티우스"
        }
      ],
      quote: '',
      speaker: '',
      date: "",
      title: "",
      list: loadData(),
      statusCnt: getStatusCnt()
    }
  },
  mounted() {
    let index = Math.floor(Math.random() * this.quotes.length)
    this.quote = this.quotes[index].saying
    this.speaker = this.quotes[index].name
  },
  methods: {
    submitForm(e){
      e.preventDefault();
      if (this.date != "" && this.title.trim() !== ""){
        const today = new Date();
        const dday = new Date(this.date)
        let diff = dday - today;
        this.statusCnt++;
        this.list.push({
          id : this.list.length,
          title : this.title,
          date : this.date,
          days : Math.floor(diff / (1000 * 60 * 60 * 24)) + 1,
          checker : false
        })
        this.list.sort(function(a, b) {
          return parseFloat(a.days) - parseFloat(b.days)
        })
        for (let i = 0; i < this.list.length; i++){
          this.list[i].id = i;
        }
        saveData(this.list);
      }
    },
    deleteTodo(idx){
      this.statusCnt--;
      for(let i = idx + 1; i < this.list.length; i++)
        this.list[i].id--;
      this.list.splice(idx, 1);
      saveData(this.list);
    },
    changeCheck(idx){
      if (this.list.at(idx).checker)
        this.statusCnt++;
      else
        this.statusCnt--;
      this.list.at(idx).checker = !this.list.at(idx).checker;
      saveData(this.list);
    }
  }
})
function loadData(){
  let load = localStorage.getItem("list");
  let loadData = [];
  if (!(load == null)){
    let loadObj = JSON.parse(load);
    for (let i = 0; i < loadObj.length; i++){
        loadData.push(loadObj[i]);
    }
  }
  return loadData
}
function saveData(list){
  let saveData = JSON.stringify(list);
  localStorage.setItem("list", saveData);
}
function getStatusCnt(){
  let list = loadData();
  let ret = list.length;
  for (let i = 0; i < list.length; i++){
    if (list[i].checker) ret--;
  }
  console.log(ret);
  return ret;
}
</script>

